.items-filter {
    border: 1px solid grey;
    border-radius: 10px;
    padding: 10px;
    position: sticky;
    margin-top: 40px;
    top: 0;
    width: 300px;
    min-height: 200px;
    height: max-content;

    .filter-list {
        display: flex;
        flex-wrap: wrap;
        .filter-item {
            text-wrap: nowrap;
            border: 1px solid grey;
            margin: 5px;
            padding: 0px 5px 0px 5px;
            border-radius: 5px;
        }
    }
}